import {
  combineUrlParams,
  mergeDeepRight,
} from '../utils';
import qs from 'query-string'

let prev = null
const createUUid = ((x) => () => x++ )(0)
export default class Request {

  type = 'http'
  keywords = []
  params = {}
  values = []
  scrollTop = 0

  /**
   * @param {String} [type] ('http'|'popstate')
   */
  constructor({
    type = 'http',
    uuid = createUUid(),
    href = location.href,
    params = {},
    scrollTop = 0,
    title = document.title
  } = {}) {
    this.prev = prev
    this.type = type
    this.uuid = uuid
    // create the params from the href and the passed in params
    this.params = mergeDeepRight(
      qs.parse(href.slice(href.indexOf('?') + 1)),
      params
    )
    // stringify the params for use in the application
    this.search = qs.stringify(this.params, {
      encode: false,
      arrayFormat: 'brackets'
    })
    this.pathname = href.slice(0, href.indexOf('?'))
    // create the new href by combining the pathname and all the params
    this.href = this.pathname + (this.search ? `?${this.search}` : '')
    this.title = title
    this.scrollTop = scrollTop
    prev = this.pathname
  }

  merge({
    params,
  }) {
    this.params = mergeDeepRight(this.params, params)
  }
}


const req = {
  type: 'http',
  pathname: location.pathname,
  href: location.href,
  params: {}
}
